@import "variables";
@import "mixins";

.home {
  .search-box-wrapper {
    margin-bottom: 60px;
    .container {
      position: relative;
    }
    .tabs-wrapper {
      position: absolute;
      top: auto;
      left: 0;
      right: 0;
      bottom: 0;
      display: block;
      margin: auto;
      z-index: 20;
      @media screen and (max-width: $screen-xs) {
        max-width: calc(100% - 30px);
      }
      .nav-tabs {
        border: none;
        .nav-item {
          border: none;
          margin-bottom: 0;
          .nav-link{
            background-color: rgba(0, 0, 0, 0.8);
            border: none;
            color: $white;
          }
        }
      }
      .tab-content {
        border: none;
        background-color: rgba(0, 0, 0, 0.8);
        padding: 30px 15px;
      }
    }
  }
}
