#wrapper {
  max-width: 2560px;
  margin-left: auto;
  margin-right: auto;
}

header {
  background-color: $white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.15);
}

.text-logo {
  color: $black;
  text-transform: uppercase;
  margin: 10px 0;
  font-size: 28px;
  font-weight: 600;
  line-height: 1.5em;
  span {
    color: $golden;
  }
}

footer {
  background-color: $black;
  font-size: 13px;
  color: $silver;
  margin-top: 90px;
  padding-bottom: 1px;
  .blue-box {
    background-color: $blue;
    position: relative;
    padding: 30px 10px;
    max-width: 240px;
    margin: 0 auto 100px;
    &:before,
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: -40px;
      bottom: auto;
    }
    &:after {
      width: 0;
      height: 0;
      border: solid transparent;
      border-width: 20px 10px;
      border-left-color: #0091ea;
      border-bottom-color: #0091ea;
      right: 1px;
      left: auto;
      transform: translateX(100%);
      z-index: -4;
    }
    &:before {
      background-color: $blue;
      left: 0;
      right: 0;
      width: 100%;
      height: 40px;
      opacity: 0.85;
    }
    .bottom-arrow {
      position: absolute;
      top: auto;
      right: 0;
      bottom: -40px;
      left: 0;
      display: block;
      width: 100%;
      height: 40px;
      overflow: hidden;
      &:after,
      &:before {
        position: absolute;
        display: inline-block;
        font-size: 0;
        content: "";
        width: 50%;
        height: 0;
        border: solid transparent;
        top: auto;
        bottom: 0;
        border-width: 20px 60px;
      }
      &:after {
        border-left-color: $blue;
        border-top-color: $blue;
        left: auto;
        right: 0;
      }
      &:before {
        border-left-color: $blue;
        border-bottom-color: $blue;
        left: 0;
        right: auto;
        transform: rotate(180deg);
      }
    }
    a {
      color: $white;
    }
  }
  .inner-box {
    padding-top: 30px;
    a {
      color: $silver;
    }
    ul {
      padding: 0;
      margin: 0;
      list-style: none;
      li {
        display: inline-block;
        a {
          display: inline-block;
        }
      }
    }
  }
  .phone {
    font-size: 36px;
    font-weight: 400;
    color: $blue;
    letter-spacing: 0.02em;
  }
}

.blue {
  color: $blue;
}

.form-control:disabled, .form-control[readonly] {
  background-color: $white;
}

section:not(.zero-padding) {
  padding: 30px 0;
}

.social-links {
  font-size: 0;
  margin-left: -7px;
  margin-right: -7px;
  margin-top: 35px;
  a {
    display: inline-block;
    margin: 7px;
    font-size: 14px;
    margin-right: 15px;
    margin-bottom: 5px;
    .fa {
      width: 34px;
      height: 34px;
      text-align: center;
      line-height: 34px;
      border-radius: 50%;
      color: #fff;
      font-size: 16px;
    }
    &:hover {
      opacity: 0.6;
    }
  }
  .fa-twitter {
    background-color: #77CBEF;
  }
  .fa-pinterest {
    background-color: #E95554;
  }
  .fa-facebook {
    background-color: #537ABC;
  }
  .fa-youtube-play {
    background-color: #E96349;
  }
  .fa.fa-twitter {
    background-color: #77CBEF;
  }
  .fa.fa-pinterest {
    background-color: #E95554;
  }
  .fa.fa-facebook {
    background-color: #537ABC;
  }
  .fa.fa-youtube-play {
    background-color: #E96349;
  }
  .fa.fa-google-plus {
    background-color: #4A4A4A;
  }
}

.menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: block;
  width: 100%;
  text-align: right;
  li {
    padding: 18px 0;
    display: inline-block;
    a {
      display: inline-block;
    }
  }
}


main {
  min-height: calc(100vh - 550px);
}

input[readonly="readonly"]{
  background-color: $lightF7 !important;
}
/** Styles only for localhost **/
.localhost {
  input[required="required"] {
    background-color: #f3dada !important;
  }
}
