
// Bootstrap overrides
//
// Copy variables from `_variables.scss` to this file to override default values
// without modifying source files.

$primary: #203375;
$secondary: #d19e01;
$success: #28A745;
$info: #17A2B8;
$warning: #FFC107;
$danger: #DC3545;
$light: #f9f9f9;
$dark: #1e1e1f;

$theme-colors: ();
$theme-colors: map-merge((
  "primary":    #203375,
  "secondary":  #d19e01,
  "success":    #28A745,
  "info":       #17A2B8,
  "warning":    #FFC107,
  "danger":     #DC3545,
  "light":      #f9f9f9,
  "dark":       #1e1e1f
), $theme-colors);

//Remove color from theme
//$theme-colors: map-remove($theme-colors, "success","info", "warning", "danger");

$grid-breakpoints: (
        xs: 0,
        sm: 641px,
        md: 641px,
        lg: 641px,
        xl: 641px
);
$container-max-widths: (
        sm: 1170px,
        md: 1170px,
        lg: 1170px,
        xl: 1170px
);
$body-bg: #FFFFFF;
$body-color: #1e1e1f;
$link-color: $primary;
$link-hover-color: darken($link-color, 15%);
$link-hover-decoration: underline;
$font-family-base: Arial;
$line-height-base: 1.6;
$headings-font-family: Arial;
$headings-font-weight: 500;
$headings-line-height: 1.1;
$border-radius: 4px;
$table-cell-padding: 8px;
$table-cell-padding-sm: 8px;
$btn-font-weight: normal;
$input-bg: #FFFFFF;
$input-color: #1e1e1f;
//$input-border-color: #1e1e1f;
$input-border-radius: 0;
$input-border-radius-lg: 0;
$input-border-radius-sm: 0;
$input-focus-border-color: #66AFE9;
$grid-gutter-width: 30px;

// Dropdowns
//
// Dropdown menu container and contents.
$dropdown-min-width: 0;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret: true;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-hover-media-query: false;
$enable-grid-classes: true;
$enable-print-styles: true;
