@import "variables";
@import "mixins";

.airport-search {
  position: relative;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  .form-control {
    padding-left: 16px;
  }
  .airports-list {
    position: absolute;
    left: 0;
    top: 36px;
    width: 100%;
    z-index: 100;
    background: #ffffff;
    .items {
      list-style: none;
      border: 1px solid #EEE;
      margin: 0;
      padding: 0;
      border-top: none;
      background-color: #FFFFFF;
      max-height: 300px;
      overflow-x: hidden;
      overflow-y: scroll;
      .item {
        border-bottom: 1px solid #eee;
        padding: 6px 8px;
        margin: 6px 0 0;
        &.is-active, &:hover {
          background: #eee;
          cursor: pointer;
        }
        &:first-of-type {
          margin-top: 0;
        }
        span {
          display: inline-block;
          &.code {
            font-weight: bold;
            color: #060606;
          }
        }
      }
    }
  }
  .airport-search-input {
    -webkit-appearance: none;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
    display: -ms-inline-flexbox;
    display: inline-flex;
    font-size: 1rem;
    height: 2.25em;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    line-height: 1.5;
    padding: calc(0.375em - 1px) calc(0.625em - 1px);
    position: relative;
    vertical-align: top;
    background-color: white;
    color: #363636;
    max-width: 100%;
    width: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    &:focus, &:active {
      box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
      outline: none;
    }
  }
}
